<template>
    <div>
        <input type="file" id="excel-upload-input" ref="excel-upload-input" accept=".xlsx, .xls" @change="handleClick">
        <div id="drop" @drop="handleDrop" @dragover="handleDragover" @dragenter="handleDragover">
            Drop excel file here or
            <el-button :loading="loading" style="margin-left: 16px;" size="mini" type="primary" @click="handleUpload">Browse</el-button>
        </div>
    </div>
</template>

<script>
import XLSX from 'xlsx'
export default {
    props: {
        beforeUpload: Function,
        onSuccess: Function
    },
    data() {
        return {
            loading: false,
            excelData: {
                header: null,
                results: null
            }
        }
    },
    methods: {
        generateDate({ header, results }) {
            this.excelData.header = header
            this.excelData.results = results
            this.onSuccess && this.onSuccess(this.excelData)
        },
        handleDrop(e) {
            e.stopPropagation()
            e.preventDefault()
            if(this.loading) return
            const files = e.dataTransfer.files
            if(files.length !== 1) {
                this.$message.error('Only support uploading one file!')
                return
            }
            const rawFile = files[0]
            this.upload(rawFile)
            e.stopPropagation()
            e.preventDefault()
        },
        handleDragover(e) {
            e.stopPropagation()
            e.preventDefault()
            e.dataTransfer.dropEffect = 'copy'
        },
        handleUpload() {
            document.getElementById('excel-upload-input').click()
        },
        handleClick(e) {
            const files = e.target.files
            const rawFile = files[0]
            if(!rawFile) return
            this.upload(rawFile)
        },
        upload(rawFile) {
            this.$refs['excel-upload-input'].value = null
            if(!this.beforeUpload) {
                this.readerData(rawFile)
                return
            }
            const before = this.beforeUpload(rawFile)
            if(before) {
                this.readerData(rawFile)
            }
        },
        readerData(rawFile) {
            this.loading = true
            return new Promise((resolve, reject) => {
                const reader = new FileReader()
                reader.onload = e => {
                    const data = e.target.result
                    const fixedData = this.fixdata(data)
                    const workbook = XLSX.read(btoa(fixedData), { type: 'base64' })
                    const firstSheetName = workbook.SheetNames[0]
                    const worksheet = workbook.sheets[firstSheetName]
                    const header = this.get_header_row(worksheet)
                    const results = XLSX.utils.sheet_to_json(worksheet)
                    this.generateDate({ hreader, results })
                    this.loading = false
                    resolve()
                }
                reader.readAsArrayBuffer(rawFile)
            })
        },
        fixdata(data) {
            let o = ''
            let l = 0
            const w = 10240
            for(; l < data.byteLength / w; ++l)
                o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)))
                o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)))
                return o
        },
        get_header_row(sheet) {
            const headers = []
            const range = XLSX.utils.decode_range(sheet['!ref'])
            let C
            const R = range.s.r
            for(C = range.s.c; C <= range.e.c; ++C) {
                var cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })]
                var hdr = 'UNKNOWN' + C
                if(cell && cell.t) hdr = XLSX.utils.format_cell(cell)
                headers.push(hdr)
            }
            return headers
        }
    }
}
</script>
<style scoped>
#excel-upload-input{
  display: none;
  z-index: -9999;
}
#drop{
  border: 2px dashed #bbb;
  width: 600px;
  height: 160px;
  line-height: 160px;
  margin: 0 auto;
  font-size: 24px;
  border-radius: 5px;
  text-align: center;
  color: #bbb;
  position: relative;
}
</style>





